/**
 * \class Settings
 * \brief Reprsentation de la configuration de l'application SGER
 * \author Zarzitski
 * \version 2
 * \date 2017-04-01 19:25:00 +0100
 */
export class Settings {
  private organization: string;
  private phone: string;
  private email: string;
  private address: string;
  private addressSupplement: string;
  private country: string;
  private postalCode: string;
  private location: string;
  private amountBoats: number;
  private timeManagement: boolean;

  /**
   * \fn     constructor();
   * \brief Constructeur qui initialise les attributs
   * \param[in] organization Le nom de l'organisation
   * \param[in] phone Le numero de tlphone de l'organisation
   * \param[in] email Le courriel de l'organisation
   * \param[in] address L'adresse de l'organisation
   * \param[in] addressSupplement Le complment adresse de l'organisation
   * \param[in] country Le pays de l'organisation
   * \param[in] postalCode Le code postal de l'organisation
   * \param[in] logo La prsence du logo de l'organisation
   * \param[in] location Lieu de droulement de l'preuve
   * \param[in] amountBoats Le nombre de bateaux dans l'preuve
   * \param[in] timeManagement Inclusion du gestionnaire de dpart
   */
  constructor(organization: string, phone: string, email: string, address: string, addressSupplement: string,
              country: string, postalCode: string, location: string, amountBoats: number, timeManagement: boolean
  ) {
    this.organization = organization;
    this.phone = phone;
    this.email = email;
    this.address = address;
    this.addressSupplement = addressSupplement;
    this.country = country;
    this.postalCode = postalCode;
    this.location = location;
    this.amountBoats = amountBoats;
    this.timeManagement = timeManagement;
  }

  /**
   * \fn     getOrganization();
   * \brief Assesseur de l'attribut organization
   * \return organization Retourne le nom de l'organisation
   */
  public getOrganization(): string {
    return this.organization;
  }

  /**
   * \fn     setOrganization();
   * \brief Mutateur de l'attribut organization
   * \param[in] organization Le nom de l'organisation
   */
  public setOrganization(value: string): void {
    this.organization = value;
  }

  /**
   * \fn     getPhone();
   * \brief Assesseur de l'attribut phone
   * \return phone Retourne le numero de tlphone de l'organisation
   */
  public getPhone(): string {
    return this.phone;
  }

  /**
   * \fn     setPhone();
   * \brief Mutateur de l'attribut phone
   * \param[in] phone Le numero de tlphone de l'organisation
   */
  public setPhone(value: string): void {
    this.phone = value;
  }

  /**
   * \fn     getEmail();
   * \brief Assesseur de l'attribut email
   * \return email Retourne le courriel de l'organisation
   */
  public getEmail(): string {
    return this.email;
  }

  /**
   * \fn     setEmail();
   * \brief Mutateur de l'attribut email
   * \param[in] email Le courriel de l'organisation
   */
  public setEmail(value: string): void {
    this.email = value;
  }

  /**
   * \fn     getAddress();
   * \brief Assesseur de l'attribut address
   * \return address Retourne l'adresse de l'organisation
   */
  public getAddress(): string {
    return this.address;
  }

  /**
   * \fn     setAddress();
   * \brief Mutateur de l'attribut address
   * \param[in] address L'adresse de l'organisation
   */
  public setAddress(value: string): void {
    this.address = value;
  }

  /**
   * \fn     getAddressSupplement();
   * \brief Assesseur de l'attribut addressSupplement
   * \return addressSupplement Retourne le complment adresse de l'organisation
   */
  public getAddressSupplement(): string {
    return this.addressSupplement;
  }

  /**
   * \fn     setAddressSupplement();
   * \brief Mutateur de l'attribut addressSupplement
   * \param[in] addressSupplement Le complment adresse de l'organisation
   */
  public setAddressSupplement(value: string): void {
    this.addressSupplement = value;
  }

  /**
   * \fn     getCountry();
   * \brief Assesseur de l'attribut country
   * \return country Retourne le pays de l'organisation
   */
  public getCountry(): string {
    return this.country;
  }

  /**
   * \fn     setCountry();
   * \brief Mutateur de l'attribut country
   * \param[in] country Le pays de l'organisation
   */
  public setCountry(value: string): void {
    this.country = value;
  }

  /**
   * \fn     getPostalCode();
   * \brief Assesseur de l'attribut postalCode
   * \return postalCode Retourne le code postal de l'organisation
   */
  public getPostalCode(): string {
    return this.postalCode;
  }

  /**
   * \fn     setPostalCode();
   * \brief Mutateur de l'attribut postalCode
   * \param[in] postalCode Le code postal de l'organisation
   */
  public setPostalCode(value: string): void {
    this.postalCode = value;
  }

  /**
   * \fn     getLocation();
   * \brief Assesseur de l'attribut location
   * \return location Retourne le lieu de droulement de l'preuve
   */
  public getLocation(): string {
    return this.location;
  }

  /**
   * \fn     setLocation();
   * \brief Mutateur de l'attribut location
   * \param[in] location Lieu de droulement de l'preuve
   */
  public setLocation(value: string): void {
    this.location = value;
  }

  /**
   * \fn     getAmountBoats();
   * \brief Assesseur de l'attribut amountBoats
   * \return amountBoats Retourne le nombre de bateaux dans l'preuve
   */
  public getAmountBoats(): number {
    return this.amountBoats;
  }

  /**
   * \fn     setAmountBoats();
   * \brief Mutateur de l'attribut amountBoats
   * \param[in] amountBoats Le nombre de bateaux dans l'preuve
   */
  public setAmountBoats(value: number): void {
    this.amountBoats = value;
  }

  /**
   * \fn     getTimeManagement();
   * \brief Assesseur de l'attribut timeManagement
   * \return phasesList Retourne l'tat inclusion du gestionnaire de dpart
   */
  public getTimeManagement(): boolean {
    return this.timeManagement;
  }

  /**
   * \fn     setTimeManagement();
   * \brief Mutateur de l'attribut phasesList
   * \param[in] timeManagement Inclusion du gestionnaire de dpart
   */
  public setTimeManagement(value: boolean): void {
    this.timeManagement = value;
  }

}



