import { Crew } from './crew';

/**
 * \class Subscriber
 * \brief Reprsentation d'un participant
 * \author Zarzitski
 * \version 1
 * \date 2017-04-01 13:08:00 +0100
 */
export class Subscriber extends Crew {
  private letter: string;

  /**
   * \fn     constructor();
   * \brief Constructeur qui initialise les attributs d'un participant
   * \param[in] id L'identifiant de l'entit
   * \param[in] name Le nom de l'quipage
   * \param[in] country Le pays de l'quipage
   * \param[in] isaf Le code ISAF de l'quipage
   * \param[in] ranking Le classement de l'quipage
   * \param[in] letter La letter du participant
   *
   */
  constructor(id: number, name: string, country: string, isaf: string, ranking: number, letter: string) {
    super(id, name, country, isaf, ranking); // Appel du constructeur Crew
    this.letter = letter;
  }

  /**
   * \fn     getLetter();
   * \brief Assesseur de l'attribut letter
   * \return letter Retourne la letter du participant
   */
  public getLetter(): string {
    return this.letter;
  }

  /**
   * \fn     setLetter();
   * \brief Mutateur de l'attribut letter
   * \param[in] letter La letter du participant
   */
  public setLetter(letter: string): void {
    this.letter = letter;
  }
}



