import { Entity } from './entity';
import { Crew } from './crew';
import { Subscriber } from './subscriber';
import { Boat } from './boat';
import { Phase } from './phase';

/**
 * \class Event
 * \brief Reprsentation d'une preuve
 * \author Zarzitski
 * \version 1
 * \date 2017-04-01 11:20:00 +0100
 */
export class Event extends Entity {
  private name: string;
  private organization: string;
  private location: string;
  private ranking: number;
  private numberBoats: number;
  private date: string;

  private subscribers: Subscriber[];
  private phasesList: Phase[];
  private boatsList: Boat[];
  private timeManagement: boolean;
  private hiddenContent: boolean;

  /**
   * \fn     constructor();
   * \brief Constructeur qui initialise les attributs de l'preuve
   * \param[in] id L'identifiant de l'entit
   * \param[in] name Le nom de l'preuve
   * \param[in] organization Le nom de l'organisation qui ralise l'preuve
   * \param[in] location Lieu de droulement de l'preuve
   * \param[in] ranking Le grade de l'preuve
   * \param[in] numberBoats Le nombre de bateaux dans l'preuve
   * \param[in] date La date de droulement du l'preuve
   * \param[in] subscribers La liste des participants de l'preuve
   * \param[in] phasesList La liste des phases de l'preuve
   * \param[in] timeManagement Inclusion du gestionnaire de dpart
   */
  constructor(id: number, name: string, organization: string, location: string, ranking: number, numberBoats: number, date: string,
              subscribers: Subscriber[], phasesList: Phase[], timeManagement: boolean
  ) {
    super(id); // Appel du constructeur Entity
    this.name = name;
    this.organization = organization;
    this.location = location;
    this.ranking = ranking;
    this.numberBoats = numberBoats;
    this.date = date;
    this.subscribers = subscribers;
    this.phasesList = phasesList;
    this.boatsList = [];
    this.timeManagement = timeManagement;
    this.hiddenContent = true;
  }

  /**
   * \fn     getName();
   * \brief Assesseur de l'attribut name
   * \return name Retourne le nom de l'preuve
   */
  public getName(): string {
    return this.name;
  }

  /**
   * \fn     setName();
   * \brief Mutateur de l'attribut name
   * \param[in] name Le nom de l'preuve
   */
  public setName(value: string): void {
    this.name = value;
  }

  /**
   * \fn     getOrganization();
   * \brief Assesseur de l'attribut organization
   * \return organization Retourne le nom de l'organisation qui ralise l'preuves
   */
  public getOrganization(): string {
    return this.organization;
  }

  /**
   * \fn     setOrganization();
   * \brief Mutateur de l'attribut organization
   * \param[in] organization Le nom de l'organisation qui ralise l'preuve
   */
  public setOrganization(value: string): void {
    this.organization = value;
  }

  /**
   * \fn     getLocation();
   * \brief Assesseur de l'attribut location
   * \return location Retourne le lieu de droulement de l'preuve
   */
  public getLocation(): string {
    return this.location;
  }

  /**
   * \fn     setLocation();
   * \brief Mutateur de l'attribut location
   * \param[in] location Lieu de droulement de l'preuve
   */
  public setLocation(value: string): void {
    this.location = value;
  }

  /**
   * \fn     getRanking();
   * \brief Assesseur de l'attribut ranking
   * \return ranking Retourne le grade de l'preuve
   */
  public getRanking(): number {
    return this.ranking;
  }

  /**
   * \fn     setRanking();
   * \brief Mutateur de l'attribut ranking
   * \param[in] ranking Le grade de l'preuve
   */
  public setRanking(value: number): void {
    this.ranking = value;
  }

  /**
   * \fn     getNumberBoats();
   * \brief Assesseur de l'attribut numberBoats
   * \return numberBoats Retourne le nombre de bateaux dans l'preuve
   */
  public getNumberBoats(): number {
    return this.numberBoats;
  }

  /**
   * \fn     setNumberBoats();
   * \brief Mutateur de l'attribut numberBoats
   * \param[in] numberBoats Le nombre de bateaux dans l'preuve
   */
  public setNumberBoats(value: number): void {
    this.numberBoats = value;
  }

  /**
   * \fn     getDate();
   * \brief Assesseur de l'attribut date
   * \return date Retourne la date du droulement de l'preuve
   */
  public getDate(): string {
    return this.date;
  }

  /**
   * \fn     setDate();
   * \brief Mutateur de l'attribut date
   * \param[in] date La date du droulement de l'preuve
   */
  public setDate(value: string): void {
    this.date = value;
  }

  /**
   * \fn     getSubscriberss();
   * \brief Assesseur de l'attribut subscribers
   * \return subscribers Retourne la liste des participants de l'preuve
   */
  public getSubscribers(): Subscriber[] {
    return this.subscribers;
  }

  /**
   * \fn     setSubscribers();
   * \brief Mutateur de l'attribut subscribers
   * \param[in] subscribers Le nom de l'preuve
   */
  public setSubscribers(value: Subscriber[]): void {
    this.subscribers = value;
  }

  /**
   * \fn     getPhasesList();
   * \brief Assesseur de l'attribut phasesList
   * \return phasesList Retourne la liste des phases de l'preuve
   */
  public getPhasesList(): Phase[] {
    return this.phasesList;
  }

  /**
   * \fn     setPhasesList();
   * \brief Mutateur de l'attribut phasesList
   * \param[in] phasesList La liste des phases de l'preuve
   */
  public setPhasesList(value: Phase[]): void {
    this.phasesList = value;
  }

  /**
   * \fn     getBoatsList();
   * \brief Assesseur de l'attribut boatsList
   * \return boatsList Retourne la liste des bateaux
   */
  public getBoatsList(): Boat[] {
    return this.boatsList;
  }

  /**
   * \fn     setBoatsList();
   * \brief Mutateur de l'attribut boatsList
   * \param[in] boatsList La liste des bateaux
   */
  public setBoatsList(value: Boat[]): void {
    this.boatsList = value;
  }

  /**
   * \fn     getTimeManagement();
   * \brief Assesseur de l'attribut timeManagement
   * \return phasesList Retourne l'tat inclusion du gestionnaire de dpart
   */
  public getTimeManagement(): boolean {
    return this.timeManagement;
  }

  /**
   * \fn     setTimeManagement();
   * \brief Mutateur de l'attribut phasesList
   * \param[in] timeManagement Inclusion du gestionnaire de dpart
   */
  public setTimeManagement(value: boolean): void {
    this.timeManagement = value;
  }

  /**
   * \fn     getHiddenContent();
   * \brief Assesseur de l'attribut hiddenContent
   * \return hiddenContent Retourne l'tat d'affichage du contenu
   */
  public getHiddenContent(): boolean {
    return this.hiddenContent;
  }

  /**
   * \fn     setHiddenContent();
   * \brief Mutateur de l'attribut hiddenContent
   * \param[in] hiddenContent L'tat d'affichage du contenu
   */
  public setHiddenContent(value: boolean): void {
    this.hiddenContent = value;
  }

}



