import { Entity } from './entity';
import { Match } from './match';

/**
 * \class Flight
 * \brief Reprsentation d'un fligth
 * \author Zarzitski
 * \version 1
 * \date 2017-04-01 14:20:00 +0100
 */
export class Flight extends Entity {
  private flightNumber: number;
  private matchsList: Match[];
  private startDateTime: string;
  private hiddenContent: boolean;
  private blueSubscriberId: number;
  private yellowSubscriberId: number;
  private blueBoatId: number;
  private yellowBoatId: number;

  /**
   * \fn     constructor();
   * \brief Constructeur qui initialise les attributs d'un flight
   * \param[in] id L'identifiant de l'entit
   * \param[in] flightNumber Le numero du flight
   * \param[in] matchsList La liste des matchs dans le fligth
   * \param[in] startDateTime Le debut de la course
   */
  constructor(id: number, flightNumber: number, matchsList: Match[], startDateTime: string) {
    super(id); // Appel du constructeur Entity
    this.flightNumber = flightNumber;
    this.matchsList = matchsList;
    this.startDateTime = startDateTime;
    this.blueSubscriberId = 0;
    this.blueBoatId = 0;
    this.yellowSubscriberId = 0;
    this.yellowBoatId = 0;
  }

  /**
   * \fn     getFlightNumber();
   * \brief Assesseur de l'attribut flightNumber
   * \return flightNumber Retourne le numero du flight
   */
  public getFlightNumber(): number {
    return this.flightNumber;
  }

  /**
   * \fn     setFlightNumber();
   * \brief Mutateur de l'attribut flightNumber
   * \param[in] flightNumber Le numero du flight
   */
  public setFlightNumber(flightNumber: number): void {
    this.flightNumber = flightNumber;
  }

  /**
   * \fn     getMatchsList();
   * \brief Assesseur de l'attribut matchsList
   * \return matchsList Retourne la liste des matches dans le fligth
   */
  public getMatchsList(): Match[] {
    return this.matchsList;
  }

  /**
   * \fn     setMatchsList();
   * \brief Mutateur de l'attribut matchsList
   * \param[in] matchsList La liste des matches dans le fligth
   */
  public setMatchsList(matchsList: Match[]): void {
    this.matchsList = matchsList;
  }

  /**
   * \fn     getStartDateTime();
   * \brief Assesseur de l'attribut matchesList
   * \return matchesList Retourne le debut de la course
   */
  public getStartDateTime(): string {
    return this.startDateTime;
  }

  /**
   * \fn     setStartDateTime();
   * \brief Mutateur de l'attribut startDateTime
   * \param[in] startDateTime Le debut de la course
   */
  public setStartDateTime(startDateTime: string): void {
    this.startDateTime = startDateTime;
  }

  /**
   * \fn     getHiddenContent();
   * \brief Assesseur de l'attribut hiddenContent
   * \return hiddenContent Retourne l'tat d'affichage du contenu
   */
  public getHiddenContent(): boolean {
    return this.hiddenContent;
  }

  /**
   * \fn     setHiddenContent();
   * \brief Mutateur de l'attribut hiddenContent
   * \param[in] hiddenContent L'tat d'affichage du contenu
   */
  public setHiddenContent(value: boolean): void {
    this.hiddenContent = value;
  }

  /**
   * \fn     getBlueSubscriberId();
   * \brief Assesseur de l'attribut blueSubscriberId
   * \return blueSubscriberId Retourne l'identifiant du participant bleu slectionn
   */
  public getBlueSubscriberId(): number {
    return this.blueSubscriberId;
  }

  /**
   * \fn     setBlueSubscriberId();
   * \brief Mutateur de l'attribut blueSubscriberId
   * \param[in] blueSubscriberId L'identifiant du participant bleu slectionn
   */
  public setBlueSubscriberId(blueSubscriberId: number): void {
    this.blueSubscriberId = blueSubscriberId;
  }

  /**
   * \fn     getBlueBoatId();
   * \brief Assesseur de l'attribut blueBoatId
   * \return blueBoatId Retourne l'identifiant du bateau bleu slectionn
   */
  public getBlueBoatId(): number {
    return this.blueBoatId;
  }

  /**
   * \fn     setBlueBoatId();
   * \brief Mutateur de l'attribut blueBoatId
   * \param[in] blueBoatId L'identifiant du bateau bleu slectionn
   */
  public setBlueBoatId(blueBoatId: number): void {
    this.blueBoatId = blueBoatId;
  }

  /**
   * \fn     getYellowSubscriberId();
   * \brief Assesseur de l'attribut yellowSubscriberId
   * \return yellowSubscriberId Retourne l'identifiant du participant jaune slectionn
   */
  public getYellowSubscriberId(): number {
    return this.yellowSubscriberId;
  }

  /**
   * \fn     setYellowSubscriberId();
   * \brief Mutateur de l'attribut yellowSubscriberId
   * \param[in] yellowSubscriberId L'identifiant du participant jaune slectionn
   */
  public setYellowSubscriberId(yellowSubscriberId: number): void {
    this.yellowSubscriberId = yellowSubscriberId;
  }

  /**
   * \fn     getYellowBoatId();
   * \brief Assesseur de l'attribut yellowBoatId
   * \return yellowBoatId Retourne l'identifiant du bateau jaune slectionn
   */
  public getYellowBoatId(): number {
    return this.yellowBoatId;
  }

  /**
   * \fn     setYellowBoatId();
   * \brief Mutateur de l'attribut yellowBoatId
   * \param[in] yellowBoatId L'identifiant du bateau jaune slectionn
   */
  public setYellowBoatId(yellowBoatId: number): void {
    this.yellowBoatId = yellowBoatId;
  }

}



