/// <reference path="./class/navMenu.ts" />
import { Component } from '@angular/core';

declare const TIME_TO_WAIT;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {

  private sidenav: boolean;
  private searchBar: boolean;
  private searchBarContent: string;
  private navMenu: {ref: NavMenu[]};

  /**
   * \fn     constructor();
   * \brief Constructeur qui initialise les attributs
   * \param settingsService La persistance de la configuration
   */
  constructor() {
    this.sidenav = false;
    this.searchBar = false;
    this.searchBarContent = '';
    this.navMenu = {ref: []};
  }

  /**
   * \fn     ngOnInit();
   * \brief Mthode qui lit les donnes la configuration
   */
  ngOnInit() {
    this.navMenu.ref.push({title: 'Home', link: 'home'});
    this.findMenuDescription(this.findMenuDescription, this.navMenu);
  }

  public pushButtonNav(link: string): void {
    this.navMenu.ref = [];
    this.searchBar = false;
    this.searchBarContent = '';
    switch(link){
      case 'home': this.navMenu.ref.push({title: 'Home', link: 'home'});
        break;
      case 'events-list':
        this.navMenu.ref.push({title: 'Events List', link: 'events-list'});
        this.searchBar = true;
        break;
      case 'crews-list':
        this.navMenu.ref.push({title: 'Crews List', link: 'crews-list'});
        this.searchBar = true;
        break;
      case 'guide': this.navMenu.ref.push({title: 'Guide', link: 'guide'});
        break;
      case 'settings': this.navMenu.ref.push({title: 'Settings', link: 'settings'});
        break;
      case 'about': this.navMenu.ref.push({title: 'About', link: 'about'});
        break;
    }
    document.getElementById('MenuData').innerHTML = '';
    this.sidenavShowHide();
  }

  private findMenuDescription(findMenuDescription, navMenu:{ref: NavMenu[]}): void {
    if(document.getElementById("MenuData")){
      if(document.getElementById('MenuData').innerHTML != ''){
        navMenu.ref = JSON.parse(document.getElementById('MenuData').innerHTML).splice(0);
      }
    }
    setTimeout(findMenuDescription.bind(null, findMenuDescription, navMenu), TIME_TO_WAIT);
  }

  public sidenavShowHide(): void {
    this.sidenav = this.sidenav ? false : true;
  }

}



