import { Entity } from './entity';

/**
 * \class Racer
 * \brief Reprsentation d'une pnalit
 * \author Zarzitski
 * \version 1
 * \date 2017-04-01 18:19:00 +0100
 */
export class Penalty extends Entity {
  private points: number;
  private detail: string;

  /**
   * \fn     constructor();
   * \brief Constructeur qui initialise les attributs de la pnalit
   * \param[in] id L'identifiant de l'entit
   * \param[in] points Les points de pnalit
   * \param[in] detail Le dtail de la pnalit
   */
  constructor(id: number, points: number, detail: string) {
    super(id); // Appel du constructeur Entity
    this.points = points;
    this.detail = detail;
  }

  /**
   * \fn     getPoints();
   * \brief Assesseur de l'attribut points
   * \return points Retourne les points de pnalit
   */
  public getPoints(): number {
    return this.points;
  }

  /**
   * \fn     setPoints();
   * \brief Mutateur de l'attribut points
   * \param[in] points Les points de pnalit
   */
  public setPoints(value: number): void {
    this.points = value;
  }

  /**
   * \fn     getDetail();
   * \brief Assesseur de l'attribut detail
   * \return detail Retourne le dtail de la pnalit
   */
  public getDetail(): string {
    return this.detail;
  }

  /**
   * \fn     setDetail();
   * \brief Mutateur de l'attribut detail
   * \param[in] detail Le dtail de la pnalit
   */
  public setDetail(value: string): void {
    this.detail = value;
  }

}



