import { Entity } from './entity';

/**
 * \class Crew
 * \brief Reprsentation d'un quipage
 * \author Zarzitski
 * \version $Rev 2$
 * \date 2017-04-01 10:31:00 +0100
 */
export class Crew extends Entity {
  private name: string;
  private country: string;
  private isaf: string;
  private ranking: number;

  /**
   * \fn     constructor();
   * \brief Constructeur qui initialise les attributs de l'quipage
   * \param[in] id L'identifiant de l'entit
   * \param[in] name Le nom de l'quipage
   * \param[in] country Le pays de l'quipage
   * \param[in] isaf Le code ISAF de l'quipage
   * \param[in] ranking Le classement de l'quipage
   */
  constructor(id: number, name: string, country: string, isaf: string, ranking: number) {
    super(id); // Appel du constructeur Entity
    this.name = name;
    this.country = country;
    this.isaf = isaf;
    this.ranking = ranking;
  }

  /**
   * \fn     getName();
   * \brief Assesseur de l'attribut name
   * \return name Retourne le nom de l'quipage
   */
  public getName(): string {
    return this.name;
  }

  /**
   * \fn     setName();
   * \brief Mutateur de l'attribut name
   * \param[in] name Le nom de l'quipage
   */
  public setName(value: string): void {
    this.name = value;
  }

  /**
   * \fn     getCountry();
   * \brief Assesseur de l'attribut country
   * \return country Retourne le pays de l'quipage
   */
  public getCountry(): string {
    return this.country;
  }

   /**
   * \fn     setCountry();
   * \brief Mutateur de l'attribut country
   * \param[in] country Le pays de l'quipage
   */
  public setCountry(value: string): void {
    this.country = value;
  }

  /**
   * \fn     getIsaf();
   * \brief Assesseur de l'attribut isaf
   * \return isaf Retourne le code ISAF de l'quipage
   */
  public getIsaf(): string {
    return this.isaf;
  }

   /**
   * \fn     setIsaf();
   * \brief Mutateur de l'attribut isaf
   * \param[in] isaf Le code ISAF de l'quipage
   */
  public setIsaf(value: string): void {
    this.isaf = value;
  }

  /**
   * \fn     getRanking();
   * \brief Assesseur de l'attribut ranking
   * \return ranking Retourne le classement de l'quipage
   */
  public getRanking(): number {
    return this.ranking;
  }

  /**
   * \fn     setRanking();
   * \brief Mutateur de l'attribut ranking
   * \param[in] ranking Le classement de l'quipage
   */
  public setRanking(value): void {
    this.ranking = value;
  }

}



