/**
 * \class Entity
 * \brief Reprsentation gnral de l'entit
 * \version $Rev 2$
 * \date 2017-04-01 10:17:00 +0100
 */
export abstract class Entity {
  protected id: number;

  /**
   * \fn     constructor();
   * \brief Constructeur qui initialise l'attribut de l'entit
   * \param[in] id L'identifiant de l'entit
   */
  constructor(id: number) {
    this.id = id;
  }

  /**
   * \fn     getId();
   * \brief Assesseur de l'attribut id
   * \return id Retourne l'identifiant de l'entit
   */
  public getId(): number {
    return this.id;
  }

  /**
   * \fn     setId();
   * \brief Mutateur de l'attribut id
   * \param[in] id L'identifiant de l'entit
   */
  public setId(id: number): void {
    this.id = id;
  }
}



