import { Subscriber } from './subscriber';
import { Boat } from './boat';
import { Penalty } from './penalty';

/**
 * \class Racer
 * \brief Reprsentation d'un coureur
 * \author Zarzitski
 * \version 1
 * \date 2017-04-01 17:48:00 +0100
 */
export class Racer extends Subscriber {
  private points: number;
  private boat: {ref: Boat};
  private penalty: Penalty;

  /**
   * \fn     constructor();
   * \brief Constructeur qui initialise les attributs du coureur
   * \param[in] id L'identifiant de l'entit
   * \param[in] points Les points du coureur
   * \param boat Le bateau du coureur
   * \param[in] penalty La pnalit du coureur
   */
  constructor(id: number, name: string, country: string, isaf: string, ranking: number, letter: string,
              points: number, boat: {ref: Boat}, penalty: Penalty
  ) {
    super(id, name, country, isaf, ranking, letter); // Appel du constructeur Crew
    this.points = points;
    this.boat = boat;
    this.penalty = penalty;
  }

  /**
   * \fn     getPoints();
   * \brief Assesseur de l'attribut points
   * \return points Retourne les points du coureur
   */
  public getPoints(): number {
    return this.points;
  }

  /**
   * \fn     setPoints();
   * \brief Mutateur de l'attribut points
   * \param[in] points Les points du coureur
   */
  public setPoints(value: number): void {
    this.points = value;
  }

  /**
   * \fn     getBoat();
   * \brief Assesseur de l'attribut boat
   * \return boat Retourne le bateau du coureur
   */
  public getBoat(): Boat {
    return this.boat.ref;
  }

  /**
   * \fn     setBoat();
   * \brief Mutateur de l'attribut boat
   * \param boat Le bateau du coureur
   */
  public setBoat(value: {ref: Boat}): void {
    this.boat = value;
  }

  /**
   * \fn     getPenalty();
   * \brief Assesseur de l'attribut penalty
   * \return penalty Retourne la pnalit du coureur
   */
  public getPenalty(): Penalty {
    return this.penalty;
  }

  /**
   * \fn     setPenalty();
   * \brief Mutateur de l'attribut penalty
   * \param[in] penalty La pnalit du coureur
   */
  public setPenalty(value: Penalty): void {
    this.penalty = value;
  }
}



