import { Entity } from './entity';
import { Boat } from './boat';

/**
 * \class Result
 * \brief Reprsentation des resultats
 * \author Sixou
 * \version 1
 * \date 2017-04-20 18:19:00 +0100
 */
 export class ResultBoat {
   private winTotBoat: number;
   private winPercentBoat: number;
   private idBoat: Boat;


 /**
  * \fn     constructor();
  * \brief Constructeur qui initialise les attributs du coureur
  * \param[in] points Les points du coureur
  * \param boat Le bateau du coureur
  * \param[in] penalty La pnalit du coureur
  */

  constructor( winTotBoat: number,  winPercentBoat: number, idBoat: Boat) {
    this.winTotBoat = winTotBoat;
    this.winPercentBoat = winPercentBoat;
    this.idBoat = idBoat;
  }

  /**
   * \fn     getWinTotBoat();
   * \brief Assesseur de l'attribut organization
   * \return organization Retourne le nom de l'organisation
   */
  public getWinTotBoat(): number {
    return this.winTotBoat;
  }

  /**
   * \fn     setWinTotBoat();
   * \brief Mutateur de l'attribut organization
   * \param[in] organization Le nom de l'organisation
   */
  public setWinTotBoat(value: number): void {
    this.winTotBoat = value;
  }

  /**
   * \fn     getWinPercentBoat();
   * \brief Assesseur de l'attribut organization
   * \return organization Retourne le nom de l'organisation
   */
  public getWinPercentBoat(): number {
    return this.winPercentBoat;
  }

  /**
   * \fn     setWinPercentBoat();
   * \brief Mutateur de l'attribut organization
   * \param[in] organization Le nom de l'organisation
   */
  public setWinPercentBoat(value: number): void {
    this.winPercentBoat = value;
  }

  /**
   * \fn     getIdBoat();
   * \brief Assesseur de l'attribut organization
   * \return organization Retourne le nom de l'organisation
   */
  public getIdBoat(): Boat {
    return this.idBoat;
  }

  /**
   * \fn     setIdBoat();
   * \brief Mutateur de l'attribut organization
   * \param[in] organization Le nom de l'organisation
   */
  public setIdBoat(value: Boat): void {
    this.idBoat = value;
  }
}



