import { Entity } from './entity';
import { Subscriber } from './subscriber';

/**
 * \class Result
 * \brief Reprsentation des resultats
 * \author Sixou
 * \version 1
 * \date 2017-04-20 18:19:00 +0100
 */
 export class ResultSubscriber extends Entity {
   private winTotSub: number;
   private winPercentSub: number;
   private idSub: Subscriber;


 /**
  * \fn     constructor();
  * \brief Constructeur qui initialise les attributs du coureur
  * \param[in] id L'identifiant de l'entit
  * \param[in] points Les points du coureur
  * \param boat Le bateau du coureur
  * \param[in] penalty La pnalit du coureur
  */

  constructor(id: number, winTotSub: number, winPercentSub: number, idSub: Subscriber) {
    super(id); // Appel du constructeur Entity
    this.winTotSub = winTotSub;
    this.winPercentSub = winPercentSub;
    this.idSub = idSub;
  }

  /**
   * \fn     getWinTotSub();
   * \brief Assesseur de l'attribut organization
   * \return organization Retourne le nom de l'organisation
   */
  public getWinTotSub(): number {
    return this.winTotSub;
  }

  /**
   * \fn     setWinTotSub();
   * \brief Mutateur de l'attribut organization
   * \param[in] organization Le nom de l'organisation
   */
  public setWinTotSub(value: number): void {
    this.winTotSub = value;
  }

  /**
   * \fn     getWinPercentSub();
   * \brief Assesseur de l'attribut organization
   * \return organization Retourne le nom de l'organisation
   */
  public getWinPercentSub(): number {
    return this.winPercentSub;
  }

  /**
   * \fn     setWinPercentSub();
   * \brief Mutateur de l'attribut organization
   * \param[in] organization Le nom de l'organisation
   */
  public setWinPercentSub(value: number): void {
    this.winPercentSub = value;
  }

  /**
   * \fn     getIdSub();
   * \brief Assesseur de l'attribut organization
   * \return organization Retourne le nom de l'organisation
   */
  public getIdSub(): Subscriber {
    return this.idSub;
  }

  /**
   * \fn     setIdSub();
   * \brief Mutateur de l'attribut organization
   * \param[in] organization Le nom de l'organisation
   */
  public setIdSub(value: Subscriber): void {
    this.idSub = value;
  }
}



