/// <reference path="./requestSQL.ts" />
declare var SQLite;
declare const TIME_TO_WAIT;

/**
 * \class DataBase
 * \brief Base de donnes SQLite
 * \author Zarzitski
 * \version 2
 * \date 2017-04-11 21:54:00 +0100
 */
export class DataBase {
  private table: string
  private dataID: string
  protected divID: string;
  /**
   * \fn     constructor();
   * \brief Constructeur qui initialise les attributs, cre la table SQL s'il n'existe pas et cre la div de stockage des chnes JSON
   * \param[in] dataID Le nom du service
   * \param[in] table La requte qui cre la table SQL s'il n'existe pas
   */
  constructor(dataID: string, table: string){
    this.table = table;
    this.dataID = dataID;
    this.writeDB([ this.table, [] ]);
    if(!document.getElementById(this.dataID))
      document.getElementById("data").innerHTML += '<div id="'+this.dataID+'" style="background-color: orange; margin: 10px;"></div>';
  }

  /**
   * \fn     readDB();
   * \brief Stock dans une div les donnes extrte de la base de donne selon la requte
   * \param[in] request La requte  excuter
   * \param[in] entitys La liste des entits  modifier
   * \param[in] applyResult La fonction qui doit tre lance  la fin du chargement
   * \param[in] divID
   */
  public readDB(request: RequestSQL, entitys: {ref: any}, applyResult, divID: string): void {
    SQLite.read(request, divID);
    this.waitResult(this.waitResult, divID, entitys, applyResult);
  }

  /**
   * \fn     writeDB();
   * \brief Crer la table SQL si elle nexiste pas et excute la requte
   * \param[in] request La requte  excuter
   */
  public writeDB(request: any): void {
    SQLite.write(this.table, request);
  }

  /**
   * \fn     waitResult();
   * \brief La fonction qui s'appelle de faon rcurrente jusqu' la dtection des donnes dans la div, puis elle excute la fonction passe dans les paramtres
   * \param[in] waitResult La fonction elle-mme
   * \param[in] divID
   * \param[in] entitys La liste des entits  modifier
   * \param[in] applyResult La fonction qui doit tre lance  la fin du chargement
   */
  private waitResult(waitResult, divID: string, entitys: any, applyResult){
    if(document.getElementById(divID).innerHTML == ''){
      setTimeout(waitResult.bind(null, waitResult, divID, entitys, applyResult), TIME_TO_WAIT);
    }
    else{
      applyResult(entitys, divID);
    }
  }

}



