import { Entity } from './entity';
import { Flight } from './flight';

/**
 * \class Phase
 * \brief Reprsentation d'une phase
 * \author Zarzitski
 * \version 1
 * \date 2017-04-01 13:35:00 +0100
 */
export class Phase extends Entity {
  private name: string;
  private roundRobin: boolean;
  private flightsList: Flight[];

  /**
   * \fn     constructor();
   * \brief Constructeur qui initialise les attributs d'une phase
   * \param[in] id L'identifiant de l'entit
   * \param[in] name Le nom de la phase
   * \param[in] roundRobin Indication si c'est un RoundRobin
   * \param[in] flightsList La liste des flights dans la phase
   */
  constructor(id: number, name: string, roundRobin: boolean, flightsList: Flight[]) {
    super(id); // Appel du constructeur Entity
    this.name = name;
    this.roundRobin = roundRobin;
    this.flightsList = flightsList;
  }

  /**
   * \fn     getName();
   * \brief Assesseur de l'attribut name
   * \return name Retourne le nom de la phase
   */
  public getName(): string {
    return this.name;
  }

  /**
   * \fn     setName();
   * \brief Mutateur de l'attribut name
   * \param[in] name Le nom de la phase
   */
  public setName(name: string): void {
    this.name = name;
  }

  /**
   * \fn     getRoundRobin();
   * \brief Assesseur de l'attribut roundRobin
   * \return roundRobin Retourne le nom de la phase
   */
  public getRoundRobin(): boolean {
    return this.roundRobin;
  }

  /**
   * \fn     setRoundRobin();
   * \brief Mutateur de l'attribut roundRobin
   * \param[in] roundRobin Indication si c'est un RoundRobin
   */
  public setRoundRobin(roundRobin: boolean): void {
    this.roundRobin = roundRobin;
  }

  /**
   * \fn     getFlightsList();
   * \brief Assesseur de l'attribut flightsList
   * \return flightsList Retourne la liste des flights dans la phase
   */
  public getFlightsList(): Flight[] {
    return this.flightsList;
  }

  /**
   * \fn     setFlightsList();
   * \brief Mutateur de l'attribut flightsList
   * \param[in] flightsList La liste des flights dans la phase
   */
  public setFlightsList(flightsList: Flight[]): void {
    this.flightsList = flightsList;
  }

}



